import { hexToRGBA } from "canvas-sketch-util/color";
import { rgbRandomPastelColor } from "../utils/tools";
import chroma from "chroma-js";
import p5 from "p5";
const canvasSketch = require("canvas-sketch");

const _ = new p5();

const settings = {
	p5: true,
	dimensions: [2048, 2048],
};

const sketch = () => {
	const c = [rgbRandomPastelColor(), rgbRandomPastelColor()];
	const cN = 3;
	const palette = chroma.scale(c).mode("lch").colors(cN);
	const gap = width / cN;
	return ({ context, width, height }) => {
		noStroke();
		for (let i = 0; i < cN; i++) {
			const [R, G, B] = hexToRGBA(palette[i]);
			fill(R, G, B, 128);
			rect(i * gap, 0, gap, height);
		}
		for (let i = 0; i < cN; i++) {
			const [R, G, B] = hexToRGBA(palette[i]);
			fill(R, G, B, 128);
			rect(0, i * gap, width, gap);
		}
	};
};

canvasSketch(sketch, settings);
